/**
 * @fileoverview This file defines the login page for the application.
 */
'use client';

import { useActionState } from 'react';
import { useFormStatus } from 'react-dom';
import { authenticate } from '@/app/actions';
import { Logo } from "@/components/logo";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertTriangle } from 'lucide-react';


function LoginButton() {
  const { pending } = useFormStatus();
 
  return (
    <Button className="w-full" aria-disabled={pending}>
      Sign In
    </Button>
  );
}

/**
 * Renders the main landing page for the application.
 * It features a login form.
 * @returns {JSX.Element} The landing page component.
 */
export default function LoginPage() {
    const [errorMessage, dispatch] = useActionState(authenticate, undefined);

    return (
    <main className="flex min-h-screen flex-col items-center justify-center bg-muted/40 p-4">
      <div className="w-full max-w-sm">
        <div className="flex justify-center mb-6">
          <Logo className="h-20 w-auto" />
        </div>
        <Card>
            <CardHeader className="text-center">
                <CardTitle className="text-2xl font-headline">Welcome to Chumplace</CardTitle>
                <CardDescription>Enter your credentials to sign in.</CardDescription>
            </CardHeader>
            <CardContent>
                <form action={dispatch} className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="email">Email</Label>
                        <Input
                            id="email"
                            type="email"
                            name="email"
                            placeholder="m@example.com"
                            required
                        />
                    </div>
                    <div className="space-y-2">
                        <Label htmlFor="password">Password</Label>
                        <Input 
                            id="password" 
                            type="password" 
                            name="password"
                            required 
                        />
                    </div>
                    
                    {errorMessage && (
                         <Alert variant="destructive">
                            <AlertTriangle className="h-4 w-4" />
                            <AlertTitle>Login Failed</AlertTitle>
                            <AlertDescription>
                                {errorMessage}
                            </AlertDescription>
                        </Alert>
                    )}

                    <LoginButton />
                </form>
            </CardContent>
        </Card>
        <p className="text-xs text-muted-foreground text-center mt-8">
          © {new Date().getFullYear()} Chumplace. All rights reserved.
        </p>
      </div>
    </main>
  );
}
